.main {
  position: fixed;
  height: 100%;
  width: 100%;
}

.header {
  position: fixed;
  height: 10%;
  width: 100%;
}

.btn {
  position: fixed;
  font-size: large;
    outline: none;
    -webkit-box-shadow: none;
    box-shadow: none;
}

.btn.clear {
  left: 1%;
}

.btn.run {
  right:1%;
}

.btn.close {
  right:1%;
}

.fullscreen {
    position: fixed;
    text-align: center;
    display: table;
}

.input {
  position: fixed;
  top: 10%;
  left: 3%;
  font-size: 3em;
  line-height: 1;
  width: 94%;
  height: 70%;

    border: none;
    overflow: auto;
    outline: none;

    -webkit-box-shadow: none;
    box-shadow: none;
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: pink;
}

.logo{
  color: black;
  top: 1%;
  font-size: 3em;
  margin: auto;
  text-align: center;
  width: 50%;
}


